export default {
    /**
     * parse
     * @description - 参数转换成Obj
     * @param path
     * @return object
     */
    parse: function (path) {
        var href = window.location.search;
        if (path)
            href = path;
        var index = href.indexOf('?');
        if (index === -1)
            return {};
        var obj = {};
        href = href.substring(index + 1);
        var strs = href.split('&');
        for (var i = 0, len = strs.length; i < len; i++) {
            var t = strs[i].split('=');
            obj[window.decodeURIComponent(t[0]).trim()] = window.decodeURIComponent(t[1]).trim();
        }
        return obj;
    },
    /**
     * stringify
     * @description - 对象转换成get参数
     * @return {String}
     * @param record
     */
    stringify: function (record) {
        var keys = Object.keys(record || {});
        var getStr = [];
        keys.forEach(function (key) {
            var value = record[key];
            if (value) {
                getStr.push(
                // @ts-ignore
                key.trim() + "=" + window.encodeURIComponent(value.trim ? value.trim() : value));
            }
        });
        return "?" + getStr.join('&');
    },
};
